div.ucwp-crypto-price-picker-main {
	display: flex;
	flex-wrap: wrap;
	justify-content: left;
	gap: 20px;
	max-width: 100vw;

	.ucwp-crypto-picker-card-coin-bounding-box {
		display: flex;
		box-sizing: border-box;
		padding: 10px 5px;
		border: 2px solid #03045E;
		border-radius: 10px;
		height: 74px;
		width: 270px;
		justify-content: center;
		background: #fff;
		transition: transform 0.3s, box-shadow 0.3s;
		animation: fadeIn 0.5s ease-in-out;

		.ucwp-crypto-picker-card-content-cnt {
			display: flex;
			width: 100%;
			padding: 0 10px;
		}

		.ucwp-crypto-picker-card-bounding-box-coin-logo {
			height: 100%;
			flex-basis: 25%;

			img {
				height: 100%;
				border-radius: 100px;
				transition: transform 0.3s;
				

				&:hover {
					transform: rotate(360deg);
					
				}
			}
		}

		.ucwp-crypto-picker-card-bounding-box-coin-info {
			flex-basis: 75%;
			display: flex;
			flex-direction: column;
			justify-content: center;
			padding: 0 5px;
			font-family: 'Poppins', sans-serif;
			font-weight: 400;
			font-size: 14px;
			transition: all 0.3s;
			

			>* {
				transition: all 0.3s;
				/* Kept from the usual code */
			}
		}

		.ucwp-crypto-picker-card-coin-name {
			display: flex;
			justify-content: space-between;
			align-items: center;
			/* Added from the usual code */
		}

		.ucwp-crypto-picker-card-coin-growth {
			display: inline-flex;
			color: green;
			font-weight: 600;
			transition: color 0.3s;
			

			&:has(.fa-arrow-up) {
				color: green;
			}

			&:has(.fa-arrow-down) {
				color: red;
			}

			i,
			span {
				display: inline-block;
				/* Added from the usual code */
			}
		}

		.ucwp-crypto-picker-card-coin-price {
			font-weight: 700;
		}
	}

	.ucwp-crypto-picker-card-coin-bounding-box-card-002 {
		display: flex;
		box-sizing: border-box;
		padding: 10px 5px;
		border: 2px solid #03045E;
