		justify-content: center;
		background: #fff;
		transition: transform 0.3s, box-shadow 0.3s;
		animation: fadeIn 0.5s ease-in-out;
		

		&:hover {
			transform: translateY(-10px);
			
			box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
			
		}

		.ucwp-crypto-picker-card-content-cnt {
			display: flex;
			width: 100%;
			padding: 0 10px;
		}

		.ucwp-crypto-picker-card-bounding-box-coin-logo {
			height: 100%;
			flex-basis: 25%;

			img {
				height: 100%;
				border-radius: 100px;
				transition: transform 0.3s;
				

				&:hover {
					transform: rotate(360deg);
					
				}
			}
		}

		.ucwp-crypto-picker-card-bounding-box-coin-info {
			flex-basis: 75%;
			display: flex;
			flex-direction: column;
			justify-content: center;
			padding: 0 5px;
			font-family: 'Poppins', sans-serif;
			font-weight: 400;
			font-size: 14px;
			transition: all 0.3s;
			

			>* {
				transition: all 0.3s;
				/* Kept from the usual code */
			}

			&:hover .ucwp-crypto-picker-card-coin-name {
				flex-basis: 100%;
				flex-grow: 1;
			}

			&:hover .ucwp-crypto-picker-card-coin-price {
				flex-basis: 0;
				display: none;
			}
		}

		.ucwp-crypto-picker-card-coin-name {
			display: flex;
			justify-content: space-between;
			align-items: center;
			/* Added from the usual code */
		}

		.ucwp-crypto-picker-card-coin-growth {
			display: inline-flex;
			color: green;
			font-weight: 600;
			transition: color 0.3s;
			

			&:has(.fa-arrow-up) {
				color: green;
			}

			&:has(.fa-arrow-down) {
				color: red;
			}

			i,
			span {
				display: inline-block;
				/* Added from the usual code */
			}
		}

		.ucwp-crypto-picker-card-coin-price {
			font-weight: 700;
		}
	}

	.ucwp-crypto-picker-card-coin-bounding-box-card-003 {
		display: flex;
		box-sizing: border-box;
		padding: 12px 10px;
		border: 2px solid #03045E;
		border-radius: 10px;
		height: 104px;
		width: 255px;
		justify-content: center;
		flex-direction: column;
		font-family: 'Poppins', sans-serif;
		font-size: 14px;
		background: #1E1E1E;
		gap: 3px;
		animation: fadeIn 0.5s ease-in-out;
		

		.ucwp-crypto-picker-card-003-section {
			width: 100%;
		}

		.ucwp-crypto-picker-card-003-basic-details {
			display: flex;
			flex-basis: 75%;
		}

		.ucwp-crypto-picker-card-bounding-box-coin-logo {
			height: 100%;
			flex-basis: 25%;

			img {
				height: 100%;
				width: 100%;
				border-radius: 100px;
				transition: transform 0.3s;
				

				&:hover {
					transform: rotate(360deg);
					
				}
			}
		}

		.ucwp-crypto-picker-card-003-basic-info {
			flex-basis: 75%;
			display: flex;
			flex-direction: column;
			justify-content: center;
			padding: 0 3px;
			align-items: center;
		}

		.ucwp-crypto-picker-card-003-coin-name {
			width: 100%;
			display: flex;
			color: #0077B6;
			justify-content: space-between;

			div {
				display: inline-flex;
				justify-content: center;
				align-items: center;
			}
		}

		.ucwp-crypto-picker-card-coin-price {
			color: #0077B6;
			font-weight: 700;
			width: 100%;
		}

		.ucwp-crypto-picker-card-coin-growth {
			flex-basis: 35%;
			font-weight: 600;

			&:has(.fa-arrow-up) {
				color: green;
			}

			&:has(.fa-arrow-down) {
				color: red;
			}

			i,
			span {
				display: inline-block;
				/* Added from the usual code */
			}
		}

		.ucwp-crypto-picker-card-coin-name-and-symbol {
			font-weight: 400;
		}

		.ucwp-crypto-picker-card-003-market-details {
			display: flex;
			font-weight: 700;
			transition: color 0.3s;
			

			>div {
				flex-basis: 50%;
				display: grid;
				place-items: center;
				color: #0077B6;
				transition: color 0.3s;
				
			}
		}
	}

	@keyframes fadeIn {
		0% {
			opacity: 0;
			transform: scale(0.9);
		}

		100% {
			opacity: 1;
			transform: scale(1);
		}
	}
}